body{
    font-family: "Montserrat",sans-serif;
    font-size: x-large;
}

.form-inner{
    margin-left: 500px;
    margin-right: 500px;
    margin-top: 150px;
    flex-direction: column;
    background-color: bisque;
    border-radius: 10px;
    padding: 10px 10px;
    justify-content: center;
    text-align: center;
    
    
}

label{
    display: inline-block;
    width: 150px;
    font-weight: bold;
}
input{
    height: 30px;
    width: 300px;
    border: 0.5px;
    border-radius: 5px;
    font-weight: bold;
    font-size: 20px;
}
.form-group{
    margin-bottom: 10px;
    align-items: center;
    justify-content: center;
}

input#login-btn{
    margin-left: 100px;
    margin-right: 100px;
    width: 150px;
    height: 40px; 
    cursor: pointer;
    transition: transform, 450ms;
}

input#login-btn:hover{
    transform: scale(1.08);
    cursor: pointer;
}

.App{
    margin-left: 500px;
    margin-right: 500px;
    margin-top: 150px;
    justify-content: center;
    text-align: center;
}
